	module.exports = {"data":{"markdownRemark":{"html":"<p>The second way to access the properties of an object is bracket notation (<code>[]</code>). If the property of the object you are trying to access has a space in it, you will need to use bracket notation.</p>\n<p>Here is a sample of using bracket notation to read an object property:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>var myObj = {\n  \"Space Name\": \"Kirk\",\n  \"More Space\": \"Spock\"\n};\nmyObj[\"Space Name\"]; // Kirk\nmyObj['More Space']; // Spock</code></pre>\n      </div>\n<p>Note that property names with spaces in them must be in quotes (single or double).</p>","fields":{"slug":"/certificates/accessing-object-properties-with-bracket-notation/"},"frontmatter":{"title":"Accessing Object Properties with Bracket Notation"}}},"pathContext":{"slug":"/certificates/accessing-object-properties-with-bracket-notation/"}}

